  box-sizing: border-box;
  margin: 0;
  line-height: normal;
  font-family: var(--font-family-sans);
  font-weight: normal;
  color: #222;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  letter-spacing: -0.5px;
}
body *,
body *::before,
body *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  word-break: keep-all;
  overflow-wrap: break-word;
}
body .blind {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
  clip-path: polygon(0 0, 0 0, 0 0);
}
body .hidden {
  display: none !important;
}
body ul,
body ol,
body li {
  list-style: none;
}
body a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}
body a:hover, body a:focus {
  outline: none;
  text-decoration: none;
}
body table {
  table-layout: fixed;
  width: 100%;
  border-collapse: collapse;
}
body button {
  border: 0;
  background-color: transparent;
  color: inherit;
  cursor: pointer;
}
body button:focus {
  outline: none;
}
body button:disabled {
  cursor: not-allowed;
}
body select,
body input,
body textarea,
body button {
  font-family: var(--font-family-sans);
  appearance: none;
}
body input:placeholder-shown {
  text-overflow: ellipsis;
}
body input[type=file] {
  position: absolute;
  width: 0;
  height: 0;
}
body input[type=number] {
  -moz-appearance: textfield;
}
body input[type=search]::-webkit-search-cancel-button {
  display: none;
}
body input[type=date] {
  appearance: none;
}
body input[type=date]::placeholder {
  display: none;
}
body input[type=date]::-webkit-date-and-time-value {
  text-align: left;
}
body input[type=date]::-webkit-calendar-picker-indicator {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-image: none;
  color: var(--placdeholder);
}
body input[type=date]::-webkit-datetime-edit {
  opacity: 1;
}
body select {
  border: 0;
  background-color: transparent;
  cursor: pointer;
}
body select:focus {
  outline: none;
}
body select::-ms-expand {
  display: none;
}
body input,
body textarea {
  font-size: inherit;
  border: 0;
  background-color: #fff;
}
body input:focus,
body textarea:focus {
  outline: none;
}
body input::placeholder,
body textarea::placeholder {
  /* Chrome, Safari, Firefox */
  -webkit-text-fill-color: currentColor;
  color: var(--placeholder);
  opacity: 1;
}
body input:-ms-input-placeholder,
body textarea:-ms-input-placeholder {
  /* IE, Edge */
  color: var(--placeholder);
  opacity: 1;
}
body input:disabled,
body textarea:disabled {
  -webkit-text-fill-color: currentColor;
  opacity: 1;
  cursor: not-allowed;
}
body input[readonly]::placeholder, body input:disabled::placeholder,
body textarea[readonly]::placeholder,
body textarea:disabled::placeholder {
  /* Chrome, Safari, Firefox */
  -webkit-text-fill-color: currentColor;
  color: rgba(34, 34, 34, 0.2);
  opacity: 1;
}
body input[readonly]:-ms-input-placeholder, body input:disabled:-ms-input-placeholder,
body textarea[readonly]:-ms-input-placeholder,
body textarea:disabled:-ms-input-placeholder {
  /* IE, Edge */
  color: rgba(34, 34, 34, 0.2);
  opacity: 1;
}
body input::-ms-clear, body input::-ms-reveal,
body textarea::-ms-clear,
body textarea::-ms-reveal {
  /* IE, Edge */
  display: none;
}
body input::-webkit-inner-spin-button, body input::-webkit-outer-spin-button,
body textarea::-webkit-inner-spin-button,
body textarea::-webkit-outer-spin-button {
  appearance: none;
}
body textarea {
  resize: none;
}
body fieldset {
  border: 0;
  min-width: 0;
}
body strong,
body b {
  font-weight: 700;
}
body em {
  font-style: normal;
